@import "../../_variables";

#chart {
  width: 100%;
  height: 500px;
}

#legend {
  display: flex;
  max-width: 100%;
  min-width: 300px;
  background: #D1D2D4;
  padding: 20px;
}

#items {
  margin-right: 10px;
}

#pager {
  display: inline-block;
  float: left;
}

.legend-item {
  padding: 6px 0 2px 6px;
  margin-bottom: 10px;
  display: inline;
}

.square {
  height: 22px;
  width: 22px;
  display: inline-block;
}

.legend-item-label {
  color: $black;
  display: inline-block;
  position: relative;
  top: -7px;
  margin: 0 15px;
}

path {
  &:nth-of-type(2) {
    transform: translate(-18px, 18px);
  }
}

.carousel-indicators .active {
  background: $report-tab-color;
}

.carousel-indicators li {
  border: 1px solid $report-tab-color;
}